import React, { createContext, useEffect, useContext } from 'react';

const ChatContext = createContext();

export const ChatProvider = ({ children }) => {
  const toggleChat = () => {
    if (window.LiveChatWidget) {
      window.LiveChatWidget.call('maximize'); // Open the chat window
    }
  };
  // Function to toggle the LiveChat widget
  useEffect(() => {
    // Initialize the LiveChat script when the component mounts
    window.__lc = window.__lc || {};
    window.__lc.license = 18319443;
    window.__lc.integration_name = "manual_onboarding";
    window.__lc.product_name = "livechat";

    (function (n, t, c) {
      function i(n) {
        return e._h ? e._h.apply(null, n) : e._q.push(n);
      }
      var e = {
        _q: [],
        _h: null,
        _v: "2.0",
        on: function () {
          i(["on", c.call(arguments)]);
        },
        once: function () {
          i(["once", c.call(arguments)]);
        },
        off: function () {
          i(["off", c.call(arguments)]);
        },
        get: function () {
          if (!e._h)
            throw new Error("[LiveChatWidget] You can't use getters before load.");
          return i(["get", c.call(arguments)]);
        },
        call: function () {
          i(["call", c.call(arguments)]);
        },
        init: function () {
          var n = t.createElement("script");
          n.async = !0;
          n.type = "text/javascript";
          n.src = "https://cdn.livechatinc.com/tracking.js";
          t.head.appendChild(n);
        },
      };
      !n.__lc.asyncInit && e.init();
      n.LiveChatWidget = n.LiveChatWidget || e;
    })(window, document, [].slice);

    // Function to toggle the LiveChat widget
    

    // Ensure the widget is maximized after initialization
    const maximizeChat = () => {
      if (window.LiveChatWidget) {
        window.LiveChatWidget.call('maximize'); // Maximize the chat window
      }
    };

    // Wait for the widget to load before maximizing
    window.LiveChatWidget.on('ready', maximizeChat);

    // Select the buttons with class 'chat'
    const chatButtons = document.querySelectorAll('.chat');

    // Attach event listeners to each button
    chatButtons.forEach((button) => {
      if (button) {
        button.addEventListener('click', toggleChat);
      }
    });

    // Cleanup: Remove event listeners when the component unmounts
    return () => {
      if (window.LiveChatWidget) {
        window.LiveChatWidget.off('ready', maximizeChat);
      }
      chatButtons.forEach((button) => {
        if (button) {
          button.removeEventListener('click', toggleChat);
        }
      });
    };
  }, []);

  return (
    <ChatContext.Provider value={{ toggleChat }}>
      {children}
    </ChatContext.Provider>
  );
};

export const useChat = () => useContext(ChatContext);
