import React, { useState } from 'react';
import { Modal, Button } from 'react-bootstrap';
import axios from 'axios';
import {
    loader,
    amazon,
    bannerFormImg,
    blackFridayBg,
    blogPosts,
    bookPackagesCta1,
    bookPackagesCta,
    bookVideoTrailer,
    creativeWritersBlogWriting,
    creativeWritersCoverDesign,
    creativeWritersEditing,
    creativeWritersLlcGw,
    creativeWritersPublishing,
    cta4Img01,
    cta4Img011,
    cta4Img02,
    cta4Img022,
    eccomerce,
    favicon,
    footerBg,
    ftrCard,
    guestBlogging,
    ibookstore,
    iconPowerBlog,
    kobo,
    landingPage,
    logo,
    longForm,
    magazineArticles,
    newsArticles,
    pillarPosts,
    playstore,
    profile1,
    profile2,
    profile3,
    profile4,
    profile5,
    profile6,
    profile7,
    profile8,
    scribd,
    seoBlogPosts,
    seoPressReleases,
    seoWebsiteContent,
    servicesIcons,
    sprite,
    viralArticles,
    webContentRewriter,
    webContentRewriterSvg,
    websiteConWri,
    websiteConWriSvg,
    whitelogo,
    Academic,
    Backgroundpopup,
    BannerHome2,
    Biographies,
    BusinessGuides,
    ChildrenBooks,
    Comics,
    Cookbooks,
    Fiction,
    NonFiction,
    SelfHelp,
} from "../../assets/images";

export const ModalSection = ({ open, onCloseModal }) => {
    const [loading, setLoading] = useState(false);
    const [errors, setErrors] = useState(false);

    const [formData, setFormData] = useState({
        name: '',
        email: '',
        message: '',
        phone: '',
    });

    const onSubmitFunc = async (e) => {
        e.preventDefault();

        console.log('formData', formData);
        if (formData.phone.length < 9) {
            setErrors(true);
        } else {
            const currentUrl = window.location.href;
            const protocol = window.location.protocol; // "https:"
            const hostname = window.location.hostname; // "creativeghostwriters.org"

            // Construct the base URL
            const baseUrl = `${protocol}//${hostname}`;
            const queryStringFormData = new URLSearchParams(formData).toString();

            let finalReq = {
                ...formData,
                source: `${currentUrl}${queryStringFormData}`,
                domain: baseUrl,
                lead_url: currentUrl,
                url: `${currentUrl}${queryStringFormData}`,
            };
            try {
                setLoading(true);
                const apiUrl = 'https://tgcrm.net/api/form_submission?brand_key=435924';
                const config = {
                    headers: {
                        'Content-Type': 'application/x-www-form-urlencoded',
                    },
                };

                const formDataString = new URLSearchParams(finalReq).toString();

                const response = await axios.post(apiUrl, formDataString, config);
                if (response.status == 200) {
                    // toast.success("Thank you for filling out the information");
                    // console.log(response.data.data.id);
                    const queryString = new URLSearchParams(
                        response.data.data
                    ).toString();
                    console.log('queryString', queryString);
                    setLoading(false);
                    setErrors(false);
                    setFormData({
                        name: '',
                        email: '',
                        message: '',
                        phone: '',
                    });
                    // naviagte(`/thank-you?${queryString}`)
                    window.location.href = `https://www.amazonbookpublishingexpert.com/thankyou?${queryString}`;
                }
                console.log('responseresponseresponse', response);
            } catch (error) {
                console.error('Error:', error);
            }
        }
    };
    return (
            <Modal className='modaL_order2' show={open} onHide={onCloseModal} aria-labelledby="contained-modal-title-vcenter" centered>
                <div class="modal-dialog">
                    <div class="modal-content">
                        <div class="modal-header">
                            <button type="button" class="btn-close" onClick={onCloseModal}>
                                <i class="fa fa-times"></i>
                            </button>
                        </div>
                        <div class="modal-body"
                        >
                            <div class="row align-items-center">
                                <div class="col-12 col-md-12 col-sm-12 col-lg-6 col-xl-6 col-xxl-6">
                                </div>
                                <div class="col-12 col-md-12 col-sm-12 col-lg-6 col-xl-6 col-xxl-6">
                                    <div class="poppup-css">
                                        <div class="wrap" id="timer-countdown">
                                        </div>
                                        <h5 class="popup-coupon-hd">Avail Amazing<span>Upto 50% Discount Now!</span></h5>
                                    </div>
                                    <form class="form_submission" method="POST" onSubmit={onSubmitFunc}>

                                        <div class="row">
                                            <div class="col-12 col-md-12 col-lg-12 col-xl-12 col-12 col-sm-12">
                                                <input type="text" placeholder="Name" name="name" required onChange={(e) =>setFormData({ ...formData, name: e.target.value })}/>
                                            </div>
                                            <div class="col-12 col-md-12 col-lg-12 col-xl-12 col-12 col-sm-12">
                                                <input type="number" minlength="10" maxlength="12" placeholder="Phone"
                                                    id="poppup-phone" name="phone" required onChange={(e) =>setFormData({...formData,phone: e.target.value,})}/>
                                            </div>
                                            <div class="col-12 col-md-12 col-lg-12 col-xl-12 col-12 col-sm-12">
                                                <input type="email" placeholder="Email" name="email" required onChange={(e) =>setFormData({ ...formData, email: e.target.value,})}/>
                                            </div>
                                            <div class="col-12 col-md-12 col-lg-12 col-xl-12 col-12 col-sm-12">
                                                <textarea name="message" placeholder="Your Message"
                                                onChange={(e) =>setFormData({ ...formData,message: e.target.value,})}></textarea>
                                            </div>
                                        </div>
                                        
                                        {loading == true ? (
                                              <div className="loader">
                                                <img alt="loader" src={loader}/>
                                              </div>
                                          ) : (
                                            <button type="submit" name="submit">Submit</button>
                                          )
                                      }
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </Modal>
    );
};