import { WEBSITE_NAME } from '../../config';
import {
    logoLogoBar,
    newLogo
} from '../../assets/images';
import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { useChat } from '../../ChatContext';
import { useLocation } from 'react-router-dom';

export default function LogoBar() {
    return (
        <section class="logobar section-second">
            <div class="container">
                <div class="row justify-content-between align-items-center gx-lg-0 g-4">
                    <div class="col-lg-2 col-12">
                        <p class="theme-p">{WEBSITE_NAME} Is Your <span>Ultimate Resource</span> To Hire Professional
                            Ghostwriters.</p>
                    </div>
                    <div class="col-lg-7 col-12">
                        <div class="logo-logobar">
                            <img class="img-fluid" src={logoLogoBar} alt="image" />
                        </div>
                    </div>
                    <div class="col-lg-2 col-12">
                        <a class="theme-btn theme-btn-featured btn-icon" href="javascript:;" data-bs-toggle="modal" data-bs-target="#getquote-popup">Get started</a>
                    </div>
                </div>
            </div>
        </section>
    );
}