import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { loader } from '../../assets/images';

export default function BannerForm() {
    const [loading, setLoading] = useState(false);
    const [errors, setErrors] = useState(false);

    const [formData, setFormData] = useState({
        name: '',
        email: '',
        message: '',
        phone: '',
    });

    const onSubmitFunc = async (e) => {
        e.preventDefault();

        console.log('formData', formData);
        if (formData.phone.length < 9) {
        setErrors(true);
        } else {
        const currentUrl = window.location.href;
        const protocol = window.location.protocol; // "https:"
        const hostname = window.location.hostname; // "authorspublishinglab.com"

        // Construct the base URL
        const baseUrl = `${protocol}//${hostname}`;
        const queryStringFormData = new URLSearchParams(formData).toString();

        let finalReq = {
            ...formData,
            source: `${currentUrl}${queryStringFormData}`,
            domain: baseUrl,
            lead_url: currentUrl,
            url: `${currentUrl}${queryStringFormData}`,
        };
        try {
            setLoading(true);
            const apiUrl = 'https://tgcrm.net/api/form_submission?brand_key=435924';
            const config = {
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded',
            },
            };

            const formDataString = new URLSearchParams(finalReq).toString();

            const response = await axios.post(apiUrl, formDataString, config);
            if (response.status == 200) {
            // toast.success("Thank you for filling out the information");
            // console.log(response.data.data.id);
            const queryString = new URLSearchParams(
                response.data.data
            ).toString();
            console.log('queryString', queryString);
            setLoading(false);
            setErrors(false);
            setFormData({
                name: '',
                email: '',
                message: '',
                phone: '',
            });
            // naviagte(`/thank-you?${queryString}`)
            window.location.href = `https://www.amazonbookpublishingexpert.com/thankyou?${queryString}`;
            }
            console.log('responseresponseresponse', response);
        } catch (error) {
            console.error('Error:', error);
        }
        }
    };
    return (
        <form class="form_submission bannerform" method="POST" onSubmit={onSubmitFunc}>
            <div class="banner-form">
                <h3 class="theme-h3">Submit Your Form</h3>
                <p class="theme-p">Discuss Your Project With Our Experts</p>
                <div class="input-field">
                    <input name="name" type="text" placeholder="Name" required onChange={(e) =>setFormData({ ...formData, name: e.target.value })}/>
                </div>
                <div class="input-field">
                    <input name="email" type="email" placeholder="Email" required onChange={(e) =>setFormData({ ...formData, email: e.target.value,})}/>
                </div>
                <div class="input-field">
                    <input name="phone" type="number" minlength="10" maxlength="12" placeholder="Phone Number" required onChange={(e) =>setFormData({...formData,phone: e.target.value,})}/>
                </div>
                <div class="input-field">
                    <textarea name="message"
                        placeholder="To Help Us Understand Better, Enter A Brief Description About Your Project." onChange={(e) =>setFormData({ ...formData,message: e.target.value,})}></textarea>
                </div>
                
                {loading == true ? (
                        <div className="loader">
                        <img alt="loader" src={loader}/>
                        </div>
                    ) : (
                        <button class="theme-btn theme-btn-featured btn-icon" type="submit">Submit<i class="fa-solid fa-chevron-right"></i></button>
                    )
                }
            </div>
        </form>
    );
}