import { PHONE, PHONE_HREF, WEBSITE_NAME, EMAIL, EMAIL_HREF, ADDRESS } from '../../config';
import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import {
    loader,
    newLogoWhite
} from '../../assets/images';
import { useChat } from '../../ChatContext';

export default function Footer() {
    const { toggleChat } = useChat();
    const [loading, setLoading] = useState(false);
    const [errors, setErrors] = useState(false);

    const [formData, setFormData] = useState({
        name: '',
        email: '',
        message: '',
        phone: '',
    });

    const onSubmitFunc = async (e) => {
        e.preventDefault();

        console.log('formData', formData);
        if (formData.phone.length < 9) {
            setErrors(true);
        } else {
            const currentUrl = window.location.href;
            const protocol = window.location.protocol; // "https:"
            const hostname = window.location.hostname; // "authorspublishinglab.com"

            // Construct the base URL
            const baseUrl = `${protocol}//${hostname}`;
            const queryStringFormData = new URLSearchParams(formData).toString();

            let finalReq = {
                ...formData,
                source: `${currentUrl}${queryStringFormData}`,
                domain: baseUrl,
                lead_url: currentUrl,
                url: `${currentUrl}${queryStringFormData}`,
            };
            try {
                setLoading(true);
                const apiUrl = 'https://tgcrm.net/api/form_submission?brand_key=435924';
                const config = {
                    headers: {
                        'Content-Type': 'application/x-www-form-urlencoded',
                    },
                };

                const formDataString = new URLSearchParams(finalReq).toString();

                const response = await axios.post(apiUrl, formDataString, config);
                if (response.status == 200) {
                    // toast.success("Thank you for filling out the information");
                    // console.log(response.data.data.id);
                    const queryString = new URLSearchParams(
                        response.data.data
                    ).toString();
                    console.log('queryString', queryString);
                    setLoading(false);
                    setErrors(false);
                    setFormData({
                        name: '',
                        email: '',
                        message: '',
                        phone: '',
                    });
                    // naviagte(`/thank-you?${queryString}`)
                    window.location.href = `https://www.amazonbookpublishingexpert.com/thankyou?${queryString}`;
                }
                console.log('responseresponseresponse', response);
            } catch (error) {
                console.error('Error:', error);
            }
        }
    };
    return (
        <main className='footer-brand'>
            <footer class="footer-main">
                <div class="container">
                    <div class="row g-4">
                        <div class="col-lg-4 col-12">
                            <a href="/" class="footer-brand">
                                <img src={newLogoWhite} alt="image" />
                            </a>
                            <p class="theme-p">Overcome the hurdle of being a non-wordsmith and fulfill your book dreams with
                                {WEBSITE_NAME}. We're your comprehensive solution, understanding and capturing your
                                ideas through our skilled wordsmiths, making your book a reality.</p>
                            <ul class="contact-links">
                                <li>
                                    <a href="javascript:;" class="chat liveChatButton" onClick={toggleChat}>
                                        <i class="fa-solid fa-comments"></i>
                                        <span>Live Chat</span>
                                    </a>
                                </li>
                                <li>
                                    <a href={PHONE_HREF}>
                                        <i class="fa-solid fa-phone-volume"></i>
                                        <span>{PHONE}</span>
                                    </a>
                                </li>
                                <li>
                                    <a href={EMAIL_HREF}>
                                        <i class="fa-solid fa-envelope"></i>
                                        <span>{EMAIL}</span>
                                    </a>
                                </li>
                                <li>
                                    <a href="javascript:;">
                                        <i class="fa-solid fa-location-dot"></i>
                                        <span>{ADDRESS}</span>
                                    </a>
                                </li>
                            </ul>
                        </div>
                        <div class="col-lg-2 col-12">
                            <h4 class="theme-h4">Quick Links</h4>
                            <ul class="quick-links">
                                <li>
                                    <Link to="/">Home</Link>
                                </li>
                                <li>
                                    <Link to="/about">About</Link>
                                </li>
                                <li>
                                    <Link to="/portfolio">Portfolio</Link>
                                </li>
                                <li>
                                    <Link to="/contact">Contact</Link>
                                </li>
                            </ul>
                        </div>
                        <div class="col-lg-2 col-12">
                            <h4 class="theme-h4">Our Services</h4>
                            <ul class="quick-links">
                                <li>
                                    <Link to="/book-publishing">Publishing</Link>
                                </li>
                                <li>
                                    <Link to="/book-marketing">Book Marketing</Link>
                                </li>
                                <li>
                                    <Link to="/ebook-editing">Editing And Manuscript</Link>
                                </li>
                                <li>
                                    <Link to="/proofreading">Proofreading Services</Link>
                                </li>
                                <li>
                                    <Link to="/ghostwriting">Ghost Writing</Link>
                                </li>
                                <li>
                                    <Link to="/ebook-writing">Ebook Writing</Link>
                                </li>
                            </ul>
                        </div>
                        <div class="col-lg-4 col-12">
                            <form class="form_submission" method="POST" onSubmit={onSubmitFunc}>
                                <div class="footer-form">
                                    <div class="form-wrapper">
                                        <h4 class="theme-h4">Get a Free Consultation</h4>
                                        <div class="input-field">
                                            <input name="name" type="text" placeholder="Name" required onChange={(e) =>setFormData({ ...formData, name: e.target.value })}/>
                                        </div>
                                        <div class="input-field">
                                            <input name="email" type="email" placeholder="Email" required onChange={(e) =>setFormData({ ...formData, email: e.target.value,})}/>
                                        </div>
                                        <div class="input-field">
                                            <input name="phone" type="number" minlength="10" maxlength="12"
                                                placeholder="Phone Number" required />
                                        </div>
                                        <div class="input-field">
                                            <textarea name="message"
                                                placeholder="To Help Us Understand Better, Enter A Brief Description About Your Project." onChange={(e) =>setFormData({ ...formData,message: e.target.value,})}></textarea>
                                        </div>
                                    </div>
                                    {loading == true ? (
                                        <div className="loader">
                                            <img alt="loader" src={loader} />
                                        </div>
                                    ) : (
                                        <button class="theme-btn theme-btn-featured btn-icon" type="submit">submit<i class="fa-solid fa-chevron-right"></i></button>
                                    )
                                    }
                                </div>
                            </form>
                        </div>
                    </div>
                    <div class="copyright">
                        <p class="theme-p"> Copyright 2024 {WEBSITE_NAME}. All Right Reserved</p>
                        <ul>
                            <li>
                                <Link to="/privacy-policy">Privacy Policy</Link>
                            </li>
                            <li>
                                <Link to="/terms-and-conditions">Terms of services</Link>
                            </li>
                        </ul>
                    </div>
                </div>
                <div class="disclaimer">
                    <div class="container">
                        <p class="theme-p"><strong>Disclaimer:</strong> Once the services provided by the
                            {WEBSITE_NAME} are delivered, they become the sole property of the clients. The
                            {WEBSITE_NAME} relinquishes any ownership rights upon successful acceptance of your work by
                            a publishing house.</p>
                    </div>
                </div>
            </footer>
        </main>

    );
}