export default function TestiSlider() {
    var settings = {
        dots: !1, 
        arrows: !0, 
        infinite: !0, 
        speed: 500, 
        slidesToShow: 3, 
        slidesToScroll: 1, 
        responsive: [
            { 
                breakpoint: 1024, 
                settings: { 
                    slidesToShow: 2, 
                    slidesToScroll: 1 
                } 
            }, 
            { 
                breakpoint: 999, 
                settings: { 
                    slidesToShow: 2, 
                    slidesToScroll: 1 
                } 
            }, 
            { 
                breakpoint: 550, 
                settings: { 
                    slidesToShow: 1, 
                    slidesToScroll: 1 
                } 
            }
        ]
    };
    return (
        <Slider className="testi-slider-here" {...settings}>
            <div>
                <div class="info-test">
                    <img src={profile1} alt="img" class="img-fluid" />
                    <h4>
                        Sarah W
                    </h4>
                    <p>"{WEBSITE_NAME} made my dream a reality! Their team is very
                        supportive. From manuscript
                        to publishing, they were cooperative throughout the process. Thank you!"
                    </p>
                </div>
            </div>
            <div>
                <div class="info-test">
                    <img src={profile2} alt="img" class="img-fluid" />
                    <h4>
                        Robert H
                    </h4>
                    <p>"Choosing {WEBSITE_NAME} was the right decision. Their commitment
                        to authors are
                        unmatchable. Grateful for their professionalism and passion."
                    </p>
                </div>
            </div>
            <div>
                <div class="info-test">
                    <img src={profile3} alt="img" class="img-fluid" />
                    <h4>
                        Emma C
                    </h4>
                    <p>"{WEBSITE_NAME} exceeded my expectations. From concept to cover,
                        their indie author
                        coach helped me at every step. A heartfelt thank you!"
                    </p>
                </div>
            </div>
            <div>
                <div class="info-test">
                    <img src={profile4} alt="img" class="img-fluid" />
                    <h4>
                        Alex M
                    </h4>
                    <p>"{WEBSITE_NAME}, you're magicians! Your editors polished my prose,
                        and your design
                        brought my story to life. A seamless experience overall!"
                    </p>
                </div>
            </div>
            <div>
                <div class="info-test">
                    <img src={profile5} alt="img" class="img-fluid" />
                    <h4>
                        Javier R
                    </h4>
                    <p>"They transformed my manuscript very professionally. Working with them was a joy, and the
                        end result was awesome. Highly recommend!"
                    </p>
                </div>
            </div>
            <div>
                <div class="info-test">
                    <img src={profile6} alt="img" class="img-fluid" />
                    <h4>
                        Ethan B
                    </h4>
                    <p>"{WEBSITE_NAME} is a team of collaborative people. I was kept in
                        loop at every step.
                        Truly remarkable!"
                    </p>
                </div>
            </div>
            <div>
                <div class="info-test">
                    <img src={profile7} alt="img" class="img-fluid" />
                    <h4>
                        Olivia S
                    </h4>
                    <p>"{WEBSITE_NAME} added magic to my words. Their team's expertise
                        turned my novel into a
                        page-turner. I am thrilled with the outcome."
                    </p>
                </div>
            </div>
            <div>
                <div class="info-test">
                    <img src={profile8} alt="img" class="img-fluid" />
                    <h4>
                        Daniel Ryan
                    </h4>
                    <p>"Their guidance was invaluable in shaping my book. Professionalism, creativity, and
                        warmth make them the perfect choice for upcoming authors. Thank you!"
                    </p>
                </div>
            </div>
        </Slider>
    );
}