import React from "react";
import Slider from "react-slick";
import {
    amazon,
    bannerFormImg,
    blackFridayBg,
    blogPosts,
    bookPackagesCta1,
    bookPackagesCta,
    bookVideoTrailer,
    creativeWritersBlogWriting,
    creativeWritersCoverDesign,
    creativeWritersEditing,
    creativeWritersLlcGw,
    creativeWritersPublishing,
    cta4Img01,
    cta4Img011,
    cta4Img02,
    cta4Img022,
    eccomerce,
    favicon,
    footerBg,
    ftrCard,
    guestBlogging,
    ibookstore,
    iconPowerBlog,
    kobo,
    landingPage,
    logo,
    longForm,
    magazineArticles,
    newsArticles,
    pillarPosts,
    playstore,
    profile1,
    profile2,
    profile3,
    profile4,
    profile5,
    profile6,
    profile7,
    profile8,
    scribd,
    seoBlogPosts,
    seoPressReleases,
    seoWebsiteContent,
    servicesIcons,
    sprite,
    viralArticles,
    webContentRewriter,
    webContentRewriterSvg,
    websiteConWri,
    websiteConWriSvg,
    whitelogo,
    Academic,
    Backgroundpopup,
    BannerHome2,
    Biographies,
    BusinessGuides,
    ChildrenBooks,
    Comics,
    Cookbooks,
    Fiction,
    NonFiction,
    SelfHelp,
} from "../../assets/images";
import {
  ADDRESS,
  EMAIL,
  EMAIL_HREF,
  PHONE,
  PHONE_HREF,
  WEBSITE_LOGO,
  WEBSITE_NAME,
} from "../../../../config";

export default function BookSlider() {
    var settings = {
        infinite: !0, 
        slidesToShow: 4, 
        arrows: !0, 
        dots: !1, 
        slidesToScroll: 1, 
        responsive: [
            { 
                breakpoint: 1024, 
                settings: { 
                    slidesToShow: 3, 
                    slidesToScroll: 3, 
                    infinite: !0, 
                    dots: !1 
                } 
            }, 
            { 
                breakpoint: 600, 
                settings: { 
                    slidesToShow: 2, 
                    slidesToScroll: 2 
                } 
            }, 
            { 
                breakpoint: 480, 
                settings: { 
                    slidesToShow: 1, 
                    slidesToScroll: 1 
                } 
            }
        ]
    };
    return (
        <Slider className="row ser_slider" {...settings}>
            <div class="col-md-12">
                <a href="javascript:;">
                    <div class="box">
                        <img loading="lazy" src={Fiction} alt="book_img" />
                        <h2>Fiction</h2>
                        <p>Write fascinating stories, and we'll help you share them with the world. Our fiction
                            collection includes romance, drama, mystery, and more. </p>
                    </div>
                </a>
            </div>
            <div class="col-md-12">
                <a href="javascript:;">
                    <div class="box">
                        <img loading="lazy" src={NonFiction} alt="book_img" />
                        <h2>Non-Fiction</h2>
                        <p>Writers and thinkers are invited to present their unique concepts and real-life
                            narratives and become published non-fiction authors.</p>
                    </div>
                </a>
            </div>
            <div class="col-md-12">
                <a href="javascript:;">
                    <div class="box">
                        <img loading="lazy" src={ChildrenBooks} alt="book_img" />
                        <h2>Childrens Books</h2>
                        <p>We emphasize contributing to society in every way possible. Publish your childrens
                            book with us to develop reading habits in the young generation.</p>
                    </div>
                </a>
            </div>
            <div class="col-md-12">
                <a href="javascript:;">
                    <div class="box">
                        <img loading="lazy" src={Comics} alt="book_img" />
                        <h2>Comics</h2>
                        <p>Explore our treasure trove of vibrant graphics and interesting scripts in the comics
                            portfolio. Bring your heroes to life with your comic publishing.</p>
                    </div>
                </a>
            </div>
            <div class="col-md-12">
                <a href="javascript:;">
                    <div class="box">
                        <img loading="lazy" src={Biographies} alt="book_img" />
                        <h2>Biographies</h2>
                        <p>Grab the opportunity to share your life journey with our Biography Publishing
                            Services and let the world know your life story.</p>
                    </div>
                </a>
            </div>
            <div class="col-md-12">
                <a href="javascript:;">
                    <div class="box">
                        <img loading="lazy" src={SelfHelp} alt="book_img" />
                        <h2>Self-Help</h2>
                        <p>Inspire personal growth and development through self-help books. Make a meaningful
                            contribution to the world self-help book publishing services.</p>
                    </div>
                </a>
            </div>
            <div class="col-md-12">
                <a href="javascript:;">
                    <div class="box">
                        <img loading="lazy" src={BusinessGuides} alt="book_img" />
                        <h2>Business Guides</h2>
                        <p>Become a guiding force for entrepreneurs by sharing your subject matter expertise and
                            success stories through business guide publishing services.</p>
                    </div>
                </a>
            </div>
            <div class="col-md-12">
                <a href="javascript:;">
                    <div class="box">
                        <img loading="lazy" src={Academic} alt="book_img" />
                        <h2>Academic</h2>
                        <p>Join {WEBSITE_NAME} as a published academic writer and
                            contribute authentic
                            scholarly pieces to our global academic portfolio.</p>
                    </div>
                </a>
            </div>
            <div class="col-md-12">
                <a href="javascript:;">
                    <div class="box">
                        <img loading="lazy" src={Cookbooks} alt="book_img" />
                        <h2>Cookbooks</h2>
                        <p>Showcase your recipes on virtual bookshelves, including Amazon and other
                            self-publishing platforms, with professional cookbook publishing.</p>
                    </div>
                </a>
            </div>
        </Slider>
    );
}