export default function WikedpediaWritingClientLogoSlider() {
  var settings = {
    loop: false,
    dots: false,
    nav: false,
    margin: 100,
    responsiveClass: true,
    autoplay: true,
    autoplayHoverPause: true,
    autoplayTimeout: 4000,
    autoplaySpeed: 400,
    responsive: {
      0: {
        items: 5,
      },
      600: {
        items: 2,
      },
      1000: {
        items: 5,
      },
      768: {
        items: 3,
      },
      320: {
        items: 1,
      },
    },
  };
  return (
    <OwlCarousel
      className="clinet-slider owl-carousel owl-theme"
      {...settings}
    >
      <li>
        <img
          loading="lazy"
          src={publishingIcon1}
          width="136"
          height="100"
          alt="publishingicon1"
        />
      </li>
      <li>
        <img
          loading="lazy"
          src={publishingIcon2}
          width="196"
          height="58"
          alt="publishingicon2"
        />
      </li>
      <li>
        <img
          loading="lazy"
          src={publishingIcon3}
          width="193"
          height="43"
          alt="publishingicon3"
        />
      </li>
      <li>
        <img
          loading="lazy"
          src={publishingIcon4}
          width="136"
          height="100"
          alt="publishingicon4"
        />
      </li>
      <li>
        <img
          loading="lazy"
          src={publishingIcon5}
          width="136"
          height="100"
          alt="publishingicon5"
        />
      </li>
    </OwlCarousel>
  );
}
