// Default CSS for the main brand
// import './App.css';
import { BrowserRouter as Router, useLocation } from "react-router-dom";
import Header from "./components/Header";
import Footer from "./components/Footer";
import AppRoutes from "./routes";
import { ChatProvider } from "./ChatContext";
import { ModalProvider } from "./ModalContext";
import { useEffect } from "react";

function App() {
  return (
    <ChatProvider>
      <ModalProvider>
        <Router>
          <MainContent />
        </Router>
      </ModalProvider>
    </ChatProvider>
  );
}

function MainContent() {
  const location = useLocation();

    useEffect(() => {
        if (location.pathname.startsWith('/lp-4')) {
        import('./pages/LP/index.css');  // Dynamically import LP CSS
        } else if (location.pathname.startsWith('/amazon-publishing')) {
        import('./pages/AmazonPublishingLP/index.css');  // Dynamically import Amazon Publishing CSS
        } else {
        import('./App.css');  // Default CSS for the brand pages
        }
    }, [location.pathname]);

  const noHeaderFooterRoutes = ["/amazon-publishing", "/amazon-publishing/", "/lp-4/", "/lp-4"];
  const shouldHideHeaderFooter = noHeaderFooterRoutes.includes(location.pathname);

  return (
    <>
      {!shouldHideHeaderFooter && <Header />}
      <AppRoutes />
      {!shouldHideHeaderFooter && <Footer />}
    </>
  );
}

export default App;

