import { Route, Routes } from 'react-router-dom';
import Home from './pages/Home';
import About from './pages/About';
import AmazonPublishing from './pages/Services/AmazonPublishing';
import AuthorWebsite from './pages/Services/AuthorWebsite';
import BookCoverDesign from './pages/Services/BookCoverDesign';
import BookEditing from './pages/Services/BookEditing';
import BookIllustration from './pages/Services/BookIllustration';
import BookMarketing from './pages/Services/BookMarketing';
import BookPrinting from './pages/Services/BookPrinting';
import BookPublishing from './pages/Services/BookPublishing';
import BookWriting from './pages/Services/BookWriting';
import EbookEditing from './pages/Services/EbookEditing';
import EbookWriting from './pages/Services/EbookWriting';
import GhostWriting from './pages/Services/GhostWriting';
import ProofReading from './pages/Services/Proofreading';
import KindleVella from './pages/KindleVella';
import WikedpediaWriting from './pages/WikedpediaWriting';
import Portfolio from './pages/Portfolio';
import Contact from './pages/Contact';
import Privacy from './pages/Privacy';
import Terms from './pages/Terms';
import AmazonPublishingLP from './pages/AmazonPublishingLP';
import LP from './pages/LP';

const AppRoutes = () => (
    <Routes>
      <Route path="/" element={<Home />} />
      <Route path="/about" element={<About />} />
      <Route path="/amazon-publishing-services" element={<AmazonPublishing />} />
      <Route path="/author-website" element={<AuthorWebsite />} />
      <Route path="/book-cover-design" element={<BookCoverDesign />} />
      <Route path="/book-editing" element={<BookEditing />} />
      <Route path="/book-illustration" element={<BookIllustration />} />
      <Route path="/book-marketing" element={<BookMarketing />} />
      <Route path="/book-printing" element={<BookPrinting />} />
      <Route path="/book-publishing" element={<BookPublishing />} />
      <Route path="/book-writing" element={<BookWriting />} />
      <Route path="/ebook-editing" element={<EbookEditing />} />
      <Route path="/ebook-writing" element={<EbookWriting />} />
      <Route path="/ghostwriting" element={<GhostWriting />} />
      <Route path="/proofreading" element={<ProofReading />} />
      <Route path="/kindle-vella" element={<KindleVella />} />
      <Route path="/wikepedia-writing" element={<WikedpediaWriting />} />
      <Route path="/portfolio" element={<Portfolio />} />
      <Route path="/contact" element={<Contact />} />
      <Route path="/privacy-policy" element={<Privacy />} />
      <Route path="/terms-and-conditions" element={<Terms />} />
      <Route path="/amazon-publishing" element={<AmazonPublishingLP />} />
      <Route path="/lp-4" element={<LP />} />
    </Routes>
);

export default AppRoutes;
