export default function KindleVellaTestSlider() {
  var settings = {
    loop: true,
    dots: false,
    nav: true,
    // navText: ['<i class="fas fa-chevron-left"></i>', '<i class="fas fa-chevron-right"></i>'],
    margin: 0,
    responsiveClass: true,
    autoplay: true,
    autoplayHoverPause: true,
    autoplayTimeout: 4000,
    autoplaySpeed: 400,
    responsive: {
        0: {
            items: 1,
        },
        600: {
            items: 2,
        },
        1000: {
            items: 1,
        },
        768: {
            items: 1,
        }
        ,
        320: {
            items: 1,
        }
    }
  };
  return (
    <OwlCarousel className="test-slider owl-carousel owl-theme" {...settings}>
      <div class="item">
        <img
          loading="lazy"
          src={writingTeam}
          width="140"
          height="140"
          alt="writing-team"
        />
        <p>
          {" "}
          With over 15 years of experience in the publishing industry, Ava has a
          keen eye for detail and a passion for helping authors refine their
          manuscripts to perfection. Her expertise lies in developmental
          editing, ensuring that every book that passes through her hands is a
          literary masterpiece.{" "}
        </p>
        <h6>Ava Sterling</h6>
        <span>Senior Editor</span>
      </div>
      <div class="item">
        <img
          loading="lazy"
          src={personGentleman2}
          width="140"
          height="140"
          alt="person-gentleman2"
        />
        <p>
          {" "}
          As our resident design maestro, Marcus brings stories to life with his
          creative and visually stunning book covers. With a background in
          graphic design and a love for storytelling, Marcus knows how to
          capture the essence of a book and convey it through striking visuals.{" "}
        </p>
        <h6>Marcus Bennett </h6>
        <span>Design Maestro</span>
      </div>
      <div class="item">
        <img
          loading="lazy"
          src={personLady}
          width="140"
          height="140"
          alt="person-lady"
        />
        <p>
          {" "}
          Elena is our go-to expert for all things marketing. With a knack for
          creating compelling marketing campaigns and a deep understanding of
          the publishing landscape, she knows how to get your book noticed in a
          crowded market.{" "}
        </p>
        <h6>Elena Rodriguez</h6>
        <span>Marketing Maven</span>
      </div>
      <div class="item">
        <img
          loading="lazy"
          src={personGentleman}
          width="140"
          height="140"
          alt="person-gentleman"
        />
        <p>
          {" "}
          Jackson is our distribution dynamo, ensuring that your book reaches
          readers far and wide. With a background in logistics and a passion for
          books, Jackson knows the ins and outs of distribution channels and
          works tirelessly to get your book into the hands of readers around the
          globe.{" "}
        </p>
        <h6>Jackson Liu </h6>
        <span>Distribution Dynamo</span>
      </div>
    </OwlCarousel>
  );
}
